body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo-static {
  -webkit-animation: App-logo-spin infinite 24s linear;
          animation: App-logo-spin infinite 24s linear;
  background: url('https://hijiangtao.github.io/rainmood/favicon.png') no-repeat center center;
  height: 40vmin;
  width: 40vmin;
  cursor: pointer;
}

.App-logo-switch:after {
  height: 40vmin;
  width: 40vmin;
  /* animation: App-logo-spin infinite 24s linear; */
  -webkit-animation: App-logo-swipe 1s linear;
          animation: App-logo-swipe 1s linear;
  -webkit-animation-fill-mode: forwards;
  background: url('https://hijiangtao.github.io/rainmood/favicon.png') no-repeat center center;
  content: '';
  display: inline-block;
  /* margin-left: -40vmin; */
  height: 40vmin;
  width: 40vmin;
  /* animation: App-logo-spin infinite 24s linear; */
}
.App-logo-switch {
  /* animation: App-logo-spin infinite 24s linear; */
  /* animation: App-logo-swipe infinite 1s linear; */
  height: 40vmin;
  /* pointer-events: none; */
  cursor: pointer;
}
.App-logo-switch:before {
  background: url('https://hijiangtao.github.io/rainmood/favicon.png') no-repeat center center;
  -webkit-animation: App-logo-swipe-reverse 1s linear;
          animation: App-logo-swipe-reverse 1s linear;
  -webkit-animation-fill-mode: forwards;
  /* animation-direction: alternate; */
  content: '';
  display: inline-block;
  margin-left: -40vmin;
  height: 40vmin;
  width: 40vmin;
  /* animation: App-logo-swipe 1s linear; */
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #666;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes App-logo-swipe {
  from {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    opacity: 0;
  }
  to {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 1;
  }
}

@keyframes App-logo-swipe {
  from {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    opacity: 0;
  }
  to {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 1;
  }
}

@-webkit-keyframes App-logo-swipe-reverse {
  from {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    opacity: 1;
  }
  to {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 0;
  }
}

@keyframes App-logo-swipe-reverse {
  from {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    opacity: 1;
  }
  to {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity: 0;
  }
}
